<script lang="js">
/**
 * @description
 * Menu des Favoris en mode connect 
 * avec un menu avec ou sans documents disponible
 * 
 * @listens emitter#service:documents:loaded
 */
export default {
  name: 'MenuBookMarkLoggued'
};
</script>

<script setup lang="js">
import MenuBookMarkNoData from '@/components/menu/bookmarks/MenuBookMarkNoData.vue';
import MenuBookMarkDataList from '@/components/menu/bookmarks/MenuBookMarkDataList.vue';

var service = inject('services');
const emitter = inject('emitter');

const IsEmpty = () => {
  var empty = true;
  for (const key in service.documents) {
    if (Object.prototype.hasOwnProperty.call(service.documents, key)) {
      const element = service.documents[key];
      if (element && element.length) {
        empty = false;
      }
    }
  }
  return empty;
};
var toggle = ref(false);
var documentsIsEmpty = computed(() => {
  // INFO
  // les promises sur les documents ne sont pas encore completement remontes !
  // on met donc une ref 'toggle' qui est complt par l'evenement mis 
  // par le service
  var value = IsEmpty();
  if (toggle.value) {
    return IsEmpty();
  }
  return value;
});

// INFO
// abonnement  l'evenement du service sur les documents afin de 
// savoir quand tous les documents sont remonts
emitter.addEventListener("service:documents:loaded", (e) => {
  toggle.value = !toggle.value;
});
// INFO
// abonnement  l'evenement du service afin de 
// mettre  jour le menu si un document a t ajout, modifi ou supprim...
emitter.addEventListener("document:saved", (e) => {
  toggle.value = !toggle.value;
});
emitter.addEventListener("document:deleted", (e) => {
  toggle.value = !toggle.value;
});
emitter.addEventListener("document:updated", (e) => {
  toggle.value = !toggle.value;
});

onMounted(() => {
})

</script>

<template>
  <div v-if="documentsIsEmpty">
    <!-- Mode connect sans documents dans l'espace personnel -->
    <MenuBookMarkNoData />
  </div>
  <div v-else>
    <!-- Mode connect avec des documents disponible -->
    <MenuBookMarkDataList
      title="Mes enregistrements"
    />
  </div>
</template>

